@font-face {
  font-family: 'Inclusive Sans';
  src: url(${___CSS_LOADER_URL_REPLACEMENT_1___}) format('woff2');
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  --main-font: 'Fredoka', system-ui;
  --secondary-font: 'Inclusive Sans', sans-serif;
  --header-font-size: 4rem;

  --header-font-weight: 600;

  --background-color: #023047;
  --cell-color: #caf0f8;
  --button-color: #219ebc;

  --player-status-color: #8ecae6;

  --error-color: #ff6666;

  --miss-color: #415a77;

  --ship-background: #979dac;
  --is-placed-color: #5c677d;
  --carrier-color: #c5172e;
  --battleship-color: #f4631e;
  --cruiser-color: #732255;
  --submarine-color: #fcf259;
  --destroyer-color: #399918;

  --main-font-color: #e0e1dd;
  --secondary-font-color: #1b263b;
  --summary-values-font-color: #ffee32;
}

body {
  background-color: var(--background-color);
  padding: 2.5% 5%;
  max-width: 1400px;
  margin: 0 auto;
}

body,
dialog {
  user-select: none;
}

button,
.cell,
.ship-model {
  cursor: pointer;
}

button:focus {
  outline: none;
}

main {
  display: grid;
  grid-template-columns: 0.75fr 1.5fr 0.75fr;
  grid-template-rows: auto 1fr;
  color: var(--main-font-color);
  position: relative;
}

header {
  grid-column: span 3;
  justify-self: center;
}

header h1 {
  font-family: var(--main-font);
  font-weight: var(--header-font-weight);
  font-size: var(--header-font-size);
  margin-bottom: 1rem;
}

.stats-container {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: auto 1fr;
  width: 75%;
  min-width: 300px;
  margin-left: auto;
  padding-right: 2rem;
  font-family: var(--secondary-font);
}

.players-stats {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  line-height: 1.25rem;
}

.player1-stats,
.player2-stats {
  padding: 1rem;
  background-color: var(--player-status-color);
  color: var(--secondary-font-color);
}

.player1 span,
.player2 span {
  font-size: 1.25rem;
  font-weight: var(--header-font-weight);
}

.hp-container {
  display: flex;
  align-items: center;
  gap: 5px;
}

.hp-bar {
  height: 1.1rem;
  border: 2px solid #0d1321;
  background-color: #d9d9d9;
  width: 100%;
  padding: 0.1rem;
}

.actual-hp {
  width: 100%;
  height: 100%;
  background-color: #0b9116;
}

.remaining-ships-container {
  display: flex;
  justify-content: space-between;
  gap: 1rem;
}

.remaining-ships-container div {
  width: 100%;
  aspect-ratio: 1 / 0.5;
  display: flex;
  justify-content: center;
  align-items: center;
}

.your-board-preview {
  padding-top: 1rem;
  text-align: center;
}

.your-board {
  display: grid;
  grid-template-columns: repeat(10, 1fr);
  gap: 1%;
  margin-top: 0.5rem;
}

.your-board .hit {
  font-size: clamp(0.25rem, 1vw, 1rem);
}

.gameplay-container {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr auto 1fr;
  justify-items: center;
  padding: 0 20px;
}

.gameboard-container,
.ship-placement-container {
  padding: 0 1rem;
  min-width: 500px;
  max-width: 800px;
}

.gameboard,
.ship-placement-board {
  display: grid;
  grid-template-columns: repeat(10, 1fr);
  gap: 1%;
  margin: 0 auto;
  justify-items: center;
}

.cell {
  background-color: var(--cell-color);
  width: 100%;
  aspect-ratio: 1 / 1;
  transition: transform 200ms;
}

.gameboard .cell:hover {
  transform: scale(1.125);
  filter: brightness(0.95);
  border-radius: 5%;
}

.gameboard .cell:active {
  transform: scale(1);
}

.disable-gameboard {
  pointer-events: none;
}

.game-stats-container {
  display: grid;
  grid-template-columns: 1fr;
  width: 100%;
  min-width: 300px;
  padding-top: 1rem;
  margin: auto;
  padding-left: 2rem;
  font-family: var(--secondary-font);
}

.turn-display {
  font-weight: var(--header-font-weight);
  font-size: 1.5rem;
  text-align: center;
}

.end-turn-btn button,
.play-again-btn button {
  border: none;
  margin: 2rem 0;
  height: 3rem;
  width: 100%;
  font-family: var(--secondary-font);
  font-size: 1.25rem;
  color: var(--secondary-font-color);
  background-color: var(--button-color);
  transition: transform 200ms;
}

.end-turn-btn button:hover,
.play-again-btn button:hover {
  transform: scale(1.05);
}

.game-message-display {
  text-align: center;
  font-family: var(--secondary-font);
  font-size: 1rem;
  margin-bottom: 2rem;
}

.winner-display {
  visibility: hidden;
}

.winner-display p:first-of-type {
  text-align: center;
  font-family: var(--main-font);
  font-size: 3rem;
  font-weight: 600;
  margin-bottom: 2rem;
}

.game-summary {
  margin-left: 1rem;
}

.game-summary span {
  color: var(--summary-values-font-color);
}

.play-again-btn {
  visibility: hidden;
}

.start-screen[open] {
  height: 100%;
  width: 100%;
  max-width: none;
  max-height: none;
  border: none;
  background-color: var(--background-color);
  color: var(--main-font-color);
}

.start-screen-content {
  display: grid;
  grid-template-rows: auto 1fr;
  justify-items: center;
  max-width: 1400px;
  margin: 0 auto;
  padding: 2.5% 5%;
}

.start-screen-content h1 {
  font-family: var(--main-font);
  font-weight: var(--header-font-weight);
  font-size: 10rem;
  color: var(--main-font-color);
}

.game-setup-container {
  display: grid;
  grid-template-columns: 0.75fr 1.5fr 0.75fr;
  width: 100%;
}

.start-new-game {
  grid-column-start: 2;
  border: none;
  margin: 0 auto;
  margin-top: 5rem;
  font-family: var(--secondary-font);
  font-size: 2rem;
  color: var(--secondary-font-color);
  height: 5rem;
  width: 80%;
  background-color: var(--button-color);
  transition: transform 200ms;
}

.start-new-game:hover {
  transform: scale(1.05);
}

.game-modes-container {
  grid-column-start: 2;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.vs-player,
.vs-computer {
  border: none;
  margin-top: 5rem;
  font-family: var(--secondary-font);
  font-size: 2rem;
  line-height: 2rem;
  color: var(--secondary-font-color);
  height: 5rem;
  width: 80%;
  background-color: var(--button-color);
  transition: transform 200ms;
}

.vs-player:hover,
.vs-computer:hover {
  transform: scale(1.05);
}

.ship-setup-container {
  padding-right: 2rem;
  width: 100%;
  min-width: 300px;
  display: grid;
  grid-template-rows: auto 1fr;
  gap: 1rem;
}

.ship-setup-label,
.input-name-label {
  justify-self: center;
  font-family: var(--secondary-font);
  margin-top: 1rem;
  font-size: 1.5rem;
}

.ships-container {
  display: grid;
  width: 100%;
  gap: 0.5rem;
  color: var(--secondary-font-color);
}

.ship {
  justify-self: center;
  display: grid;
  grid-template-columns: 0.75fr 0.75fr 1fr;
  width: 100%;
  padding: 3% 5%;
  white-space: nowrap;
  background-color: var(--ship-background);
  gap: 0.5rem;
}

.ship-name {
  font-family: var(--secondary-font);
  font-size: 1.25rem;
  line-height: 1.25rem;
}

.ship-health {
  font-family: var(--secondary-font);
  font-size: 0.75rem;
  line-height: 1.25rem;
}

.ship-btns {
  justify-self: end;
  display: flex;
  gap: 0.25rem;
}

.change-orientation-btn,
.undo-btn {
  width: 1.5rem;
  height: 1.5rem;
  border: none;
  transition: transform 100ms;
}

.change-orientation-btn:hover,
.undo-btn:hover {
  transform: scale(1.1);
  background-color: bisque;
}

.ship-model {
  grid-column: span 3;
  height: 2.5rem;
  transition: transform 200ms;
}

.ship-model:hover {
  transform: scale(1.05);
}

.ship-placement-board {
  margin-top: 1rem;
}

.name-start-container {
  padding: 0 10%;
  width: 100%;
  min-width: 300px;
}

.name-start-container input {
  user-select: text;
  height: 2.5rem;
  width: 100%;
  font-family: var(--secondary-font);
  font-size: 1.75rem;
  text-align: center;
  padding: 1.5rem 5% 1.5rem 5%;
  margin-top: 1rem;
  border: none;
}

.name-start-container input:focus {
  background-color: whitesmoke;
  outline: none;
  border: none;
  transform: scale(1.1);
}

.action-btn-container {
  margin-top: 5rem;
}

.action-btn {
  border: none;
  height: 3.5rem;
  width: 100%;
  font-family: var(--secondary-font);
  font-size: 2rem;
  color: var(--secondary-font-color);
  background-color: var(--button-color);
  transition: transform 100ms;
}

.action-btn:hover {
  transform: scale(1.1);
}

.error-msg {
  margin-top: 1rem;
  color: var(--error-color);
  font-family: var(--secondary-font);
  font-size: 1.25rem;
  text-align: center;
}

/* Board state during ship placement and gameplay */
.has-ship {
  transform: scale(1.125);
}

.carrier {
  background-color: var(--carrier-color);
}

.battleship {
  background-color: var(--battleship-color);
}

.cruiser {
  background-color: var(--cruiser-color);
}

.submarine {
  background-color: var(--submarine-color);
}

.destroyer {
  background-color: var(--destroyer-color);
}

.is-placed {
  background-color: var(--is-placed-color);
  pointer-events: none;
}

.miss {
  background-color: var(--miss-color);
}

.hit {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: clamp(1rem, 2vw, 2rem);
}

.sunk {
  filter: brightness(0.75);
}

/* Loading screen */
.loading-screen[open] {
  height: 100%;
  width: 100%;
  max-width: none;
  max-height: none;
  border: none;
  background-color: var(--background-color);
  color: var(--main-font-color);
}

.loading-screen-content {
  max-width: 1400px;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0 auto;
  padding: 2.5% 5%;
}

.loading-screen-content h1 {
  font-family: var(--main-font);
  font-weight: var(--header-font-weight);
  font-size: var(--header-font-size);
}

.loading-screen-message {
  font-family: var(--secondary-font);
  font-size: 2rem;
  margin: 5rem 0 2rem 0;
}

.loading-screen button {
  display: none;
  border: none;
  margin: 2rem 0;
  height: 3rem;
  width: 20%;
  font-family: var(--secondary-font);
  font-size: 1.5rem;
  background-color: var(--button-color);
  color: var(--secondary-font-color);
  transition: transform 100ms;
}

.loading-screen button:hover {
  transform: scale(1.1);
}

.loader-message {
  font-family: var(--secondary-font);
  font-size: 1.5rem;
  margin-bottom: 1rem;
}

.loader-animation {
  width: 60px;
  margin: auto;
  aspect-ratio: 4;
  background: radial-gradient(circle closest-side, #fdfaf6 90%, #0000) 0 /
    calc(100% / 3) 100% space;
  clip-path: inset(0 100% 0 0);
  animation: l1 1s steps(4) infinite;
}

@keyframes l1 {
  to {
    clip-path: inset(0 -34% 0 0);
  }
}

@media (max-width: 767px), (orientation: portrait) {
  :root {
    --header-font-size: 3rem;
  }

  main {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: repeat(4, auto);
    gap: 1rem;
  }

  header {
    grid-column: auto;
  }

  .stats-container {
    grid-row: 4;
    width: 100%;
    min-width: none;
    padding-right: 0;
    grid-template-columns: 1.1fr 0.9fr;
    gap: 1rem;
  }

  .player1-stats,
  .player2-stats {
    width: 100%;
    padding: 0.5rem;
  }

  .remaining-ships-container {
    gap: 0.5rem;
  }

  .remaining-ships-container div {
    aspect-ratio: 1 / 0.5;
    font-size: 0.5rem;
  }

  .gameboard-container,
  .ship-placement-container {
    padding: 1rem 0 calc(1rem + 1vh + 4vw) 0; /* Prevent gameboard grid overflow*/
    width: 100%;
    min-width: 50px;
    max-width: none;
  }

  .gameboard-container {
    grid-row: 3;
  }

  .your-board-preview {
    padding-top: 0;
  }

  .your-board-preview p {
    display: none;
  }

  .your-board {
    margin-top: 0;
  }

  .game-stats-container {
    grid-row: 2;
    grid-template-columns: 1.25fr 0.75fr;
    grid-template-rows: auto auto 0px;
    grid-template-areas:
      'player end-turn'
      'message message';
    align-items: center;
    margin-right: 0;
    padding-top: 0;
    padding-left: 0;
    width: 100%;
    min-width: none;
  }

  .turn-display {
    grid-area: player;
    text-align: left;
    font-size: 1.25rem;
  }

  .turn-display span::after {
    content: ' ';
  }

  .turn-display br {
    display: none;
  }

  .end-turn-btn {
    grid-area: end-turn;
    width: 100%;
  }

  .end-turn-btn button {
    margin: 0;
    font-size: 1rem;
  }

  .game-message-display {
    grid-area: message;
    justify-self: bottom;
    margin-bottom: 0;
    margin-top: 0.5rem;
    height: 1rem;
  }

  .winner-display {
    position: absolute;
    left: 50%;
    top: 45%;
    transform: translate(-50%, -50%);
    display: flex;
    flex-direction: column;
    background-color: var(--background-color);
    width: 80%;
    padding: 1rem;
  }

  .play-again-btn {
    align-self: center;
    width: 100%;
  }

  .play-again-btn button {
    margin-bottom: 0;
  }

  .start-screen-content h1 {
    font-size: var(--header-font-size);
  }

  .game-setup-container {
    grid-template-columns: 1fr;
    grid-template-rows: repeat(3, auto);
    justify-items: center;
    gap: 1rem;
  }

  .start-new-game,
  .vs-player,
  .vs-computer {
    font-size: 1.75rem;
  }

  .start-new-game,
  .game-modes-container {
    grid-column-start: 1;
  }

  .game-modes-container {
    width: 80%;
  }

  .ship-setup-container {
    grid-row: 2;
    padding-right: 0;
    min-width: none;
    gap: 0;
  }

  .ship-setup-label {
    display: none;
  }

  .ship-placement-container::before {
    content: 'Position your ships!';
    display: block;
    font-family: var(--main-font);
    color: var(--main-font-color);
    font-size: 1.75rem;
    text-align: center;
  }

  .ship-placement-board {
    width: 100%;
  }

  .ships-container {
    grid-template-columns: auto auto;
    margin: auto;
  }

  .ship {
    padding: 0.5rem;
    grid-template-columns: 1fr auto;
    grid-template-rows: repeat(3, auto);
    gap: 0.25rem;
  }

  .ship:first-of-type {
    grid-column: span 2;
  }

  .ship-name {
    font-size: 1rem;
    font-weight: var(--header-font-weight);
    line-height: 1rem;
  }

  .ship-health {
    line-height: 1rem;
    grid-row: 2;
  }

  .ship-btns {
    flex-direction: column;
    grid-row: 1;
    grid-row: span 3;
  }

  .change-orientation-btn,
  .undo-btn {
    width: 3rem;
    height: 100%;
  }

  .ship-model {
    grid-column: auto;
    width: 100% !important;
  }

  .name-start-container {
    min-width: none;
  }

  .input-name-label {
    margin-top: 0;
  }

  .name-start-container input {
    font-size: 2rem;
    margin-top: 0.5rem;
  }

  .action-btn-container {
    margin-top: 2rem;
  }

  .error-msg {
    margin-bottom: 2rem;
  }

  .loading-screen-message {
    text-align: center;
  }

  .loading-screen button {
    width: 80%;
  }
}

@media (min-width: 768px) and (max-width: 1200px) and (orientation: landscape) {
  .start-screen-content h1 {
    font-size: 8rem;
  }

  .vs-player,
  .vs-computer {
    margin-top: calc(2rem + 3vh);
  }

  .ship-setup-container,
  .name-start-container {
    min-width: 200px;
  }

  .ship-setup-container {
    padding-right: 0;
  }

  .name-start-container {
    padding: 0;
  }

  .ship-setup-label,
  .input-name-label {
    font-size: 1.25rem;
  }

  .ship {
    padding: 0.5rem;
    grid-template-columns: 1fr auto;
    gap: 0.25rem;
  }

  .ship-name {
    font-weight: var(--header-font-weight);
    font-size: 1rem;
  }

  .ship-health {
    margin-right: 0.25rem;
  }

  .ship-model {
    height: 2rem;
  }

  .gameboard-container,
  .ship-placement-container {
    min-width: 300px;
  }

  .stats-container,
  .game-stats-container {
    min-width: 200px;
    padding: 0;
  }

  .stats-container {
    width: 100%;
  }

  .action-btn {
    font-size: 1.5rem;
  }

  .player1 span,
  .player2 span {
    font-size: 1.1rem;
  }

  .remaining-ships-container div {
    aspect-ratio: 1 / 1;
  }

  .your-board-preview {
    padding: 1rem;
  }
}
